"use client"

import { useState } from "react"

export default function SplitPDF() {
  const [file, setFile] = useState<File | null>(null)
  const [pageRanges, setPageRanges] = useState("")
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      if (e.target.files[0].type !== "application/pdf") {
        setError("Please upload a PDF file")
        return
      }
      setFile(e.target.files[0])
      setError("")
    }
  }

  const handleSplit = async () => {
    if (!file) {
      setError("Please upload a PDF file")
      return
    }
    if (!pageRanges.trim()) {
      setError("Please enter page ranges")
      return
    }

    try {
      setLoading(true)
      setError("")

      const formData = new FormData()
      formData.append("file", file)
      formData.append("pageRanges", pageRanges)

      const response = await fetch("/api/split", {
        method: "POST",
        body: formData,
      })

      if (!response.ok) {
        throw new Error("Failed to split PDF file")
      }

      const blob = await response.blob()
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement("a")
      a.href = url
      a.download = "split.pdf"
      document.body.appendChild(a)
      a.click()
      window.URL.revokeObjectURL(url)
      document.body.removeChild(a)

      setFile(null)
      setPageRanges("")
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">Split PDF File</h1>
        <p className="text-lg text-gray-600">
          Separate one page or a whole set for easy conversion into independent PDF files.
        </p>
      </div>

      <div className="max-w-3xl mx-auto">
        <div className="bg-white rounded-lg shadow-sm border-2 border-dashed border-gray-300 p-12">
          <div className="text-center">
            <svg
              className="mx-auto h-12 w-12 text-gray-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M8 7v8a2 2 0 002 2h6M8 7V5a2 2 0 012-2h4.586a1 1 0 01.707.293l4.414 4.414a1 1 0 01.293.707V15a2 2 0 01-2 2h-2M8 7H6a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2v-2"
              />
            </svg>
            <div className="mt-4">
              <label htmlFor="file-upload" className="cursor-pointer">
                <span className="mt-2 block text-sm font-medium text-gray-900">
                  Drop PDF file here
                </span>
                <span className="mt-1 block text-sm text-gray-500">
                  or click to select file
                </span>
                <input
                  id="file-upload"
                  name="file-upload"
                  type="file"
                  className="hidden"
                  accept=".pdf"
                  onChange={handleFileChange}
                />
              </label>
            </div>
          </div>

          <div className="mt-6">
            <label htmlFor="page-ranges" className="block text-sm font-medium text-gray-700">
              Page Ranges (e.g., 1-3,5,7-9)
            </label>
            <input
              id="page-ranges"
              type="text"
              className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
              value={pageRanges}
              onChange={(e) => setPageRanges(e.target.value)}
              placeholder="1-3,5,7-9"
            />
          </div>

          {error && (
            <div className="mt-4 p-4 bg-red-50 rounded-md">
              <p className="text-sm text-red-600">{error}</p>
            </div>
          )}

          {file && (
            <div className="mt-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4">Selected File:</h3>
              <div className="flex items-center justify-between py-2 px-4 bg-gray-50 rounded">
                <span className="text-sm text-gray-900">{file.name}</span>
                <span className="text-sm text-gray-500">{(file.size / 1024 / 1024).toFixed(2)} MB</span>
              </div>
              <button
                type="button"
                className={`mt-6 w-full inline-flex justify-center items-center px-6 py-3 border border-transparent text-base font-medium rounded-md text-white ${
                  loading ? "bg-blue-400 cursor-not-allowed" : "bg-blue-600 hover:bg-blue-700"
                }`}
                onClick={handleSplit}
                disabled={loading}
              >
                {loading ? "Splitting..." : "Split PDF File"}
              </button>
            </div>
          )}
        </div>

        <div className="mt-8 text-center">
          <h2 className="text-lg font-medium text-gray-900 mb-4">How to Split PDF Files</h2>
          <ol className="text-left space-y-4">
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">1</span>
              <span className="text-gray-600">Upload the PDF file you want to split</span>
            </li>
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">2</span>
              <span className="text-gray-600">Enter the page ranges you want to extract</span>
            </li>
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">3</span>
              <span className="text-gray-600">Click "Split PDF File" and download your extracted PDF</span>
            </li>
          </ol>
        </div>
      </div>
    </div>
  )
}
